.footer-navigation-item__link {
  font-size: 16px;
  line-height: 20px;
  color: #f9fbfd;

  &:hover,
  &:focus {
    opacity: 0.7;
  }

  @media (max-width: $tablet-width) {
    font-size: 12px;
    line-height: 15px;
  }

  @media (max-width: $mobile-width) {
    flex-direction: column;
    padding: 0;
  }
}
